\n  -webkit-text-size-adjust: 100%;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  text-rendering: optimizeLegibility;\n  scroll-behavior: smooth;\n}\n\n/* Body defaults */\nbody {\n  min-height: 100vh;\n  text-rendering: optimizeSpeed;\n  line-height: $line-height-base;\n  background-color: $bg;\n  color: $text;\n  padding: 24px;\n}\n\n/* Anchor defaults */\na {\n  text-decoration: none;\n  color: inherit;\n}\n\n/* Image and media elements */\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n  height: auto;\n}\n\n/* Form elements */\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n","// Colors\n$transparent: transparent;\n$bg: #0c0d0e;\n$dark-charcoal: #101214;\n$super-dark-gray: #1a1a1a;\n$dim-gray: #555555;\n$light-gray: #bcb068;\n$gray: #d1d1d1;\n$accent: #f7f7f7;\n$text: #cccccc;\n$primary: #efece6;\n$outline: hsl(0, 0%, 82%);\n$border-opacity: rgba(209, 209, 209, 0.26);\n$gradient: #d0c7b3;\n$gradient2: #968a73;\n$gradient3: #675e4c;\n\n// Typography\n$letter-spacing-close: -3.5%;\n$letter-spacing-base: -1%;\n\n$line-height-base: 150%;\n$line-height-close: 96%;\n\n// Shadows\n$box-shadow-shine: 0px 0px 58px 20px rgba(85, 85, 85, 0.2);\n$box-shadow-bright: 0px 0px 58px 20px rgba(85, 85, 85, 0.35);\n","@use 'variable' as *;\n@use 'base' as *;\n@use 'mixins' as *;\n\nheader {\n  @include flex-between;\n  height: 50px;\n}\n\n#logo {\n  font-size: 1.5rem;\n  font-weight: bold;\n  color: $primary;\n}\n\nnav .about {\n  display: none;\n}\n\nhr {\n  margin: 24px 0;\n  border: none;\n  border-top: 1px solid $border-opacity;\n}\n\nnav {\n  display: flex;\n  gap: 10px;\n  align-items: center;\n}\n\nnav .btn {\n  background-color: $gradient;\n  color: $super-dark-gray;\n  padding: 0.75rem 1.3rem;\n  border-radius: 0.5rem;\n  font-size: 0.875rem;\n  font-weight: 600;\n  transition: background-color 0.3s ease;\n\n  &:hover {\n    background-color: $light-gray;\n    color: $primary;\n  }\n}\n\n.theme-toggle {\n  background-color: $gradient;\n  color: $super-dark-gray;\n  padding: 0.75rem 1.2rem;\n  border-radius: 0.5rem;\n  font-size: 0.875rem;\n  font-weight: 600;\n  border: none;\n  cursor: pointer;\n  transition: background-color 0.3s ease;\n\n  &:hover {\n    background-color: $light-gray;\n  }\n}\n\n.hero {\n  margin-left: auto;\n  margin-right: auto;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  margin-top: 5rem;\n  margin-bottom: 2.5rem;\n\n  .hero__headline {\n    font-size: 2.5rem;\n    max-width: 20ch;\n    text-align: center;\n    color: $accent;\n    line-height: $line-height-close;\n    font-weight: 700;\n    margin-bottom: 24px;\n\n    span {\n      color: $gradient2;\n    }\n  }\n\n  p {\n    color: $accent;\n    margin-left: auto;\n    margin-right: auto;\n    font-size: 1.2rem;\n    text-align: center;\n    padding-top: 1.25rem;\n    max-width: 60ch;\n  }\n}\n\n.card-container {\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\n  gap: 24px;\n  padding: 24px 0;\n}\n\n.card {\n  background-color: $dark-charcoal;\n  border: 4px solid $border-opacity;\n  border-radius: 1rem;\n  overflow: hidden;\n  width: 100%;\n  transition:\n    transform 0.3s ease,\n    box-shadow 0.3s ease;\n  box-shadow: $box-shadow-shine;\n  cursor: pointer;\n\n  &:hover {\n    transform: translateY(-4px);\n    box-shadow: $box-shadow-bright;\n    border-color: $outline;\n  }\n}\n\n.card__header {\n  @include flex-center;\n  border-radius: 0 0 1rem 1rem;\n  background-color: $super-dark-gray;\n  padding-top: 45%;\n  padding-bottom: 45%;\n  position: relative;\n\n  .image_container {\n    position: absolute;\n    width: 70%;\n    border-radius: 1.5rem;\n    overflow: hidden;\n    background-color: transparent;\n  }\n\n  .image_container:hover {\n    transition: all 0.2s ease-in-out;\n    box-shadow: $box-shadow-bright;\n  }\n\n  .image_container img {\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    transition: transform 0.2s ease-in-out;\n  }\n}\n\n.card__logo {\n  position: absolute;\n  top: 0.9rem;\n  right: 1rem;\n  padding: 0.3rem 0.7rem;\n  border-radius: 0.75rem;\n  background-color: $gradient3;\n  backdrop-filter: blur(40px);\n  transition: opacity 0.2s ease-in-out;\n  z-index: 99;\n}\n\n.card__name {\n  font-size: 1.25rem;\n  font-weight: 600;\n  color: $primary;\n  letter-spacing: $letter-spacing-base;\n}\n\n.card__content {\n  background-color: $dark-charcoal;\n  padding: 1.5rem;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  align-items: flex-start;\n}\n\n.card__description {\n  font-size: 1.1rem;\n  font-weight: 700;\n  color: $text;\n  line-height: $line-height-base;\n  margin-bottom: 16px;\n}\n\n.card__text {\n  font-size: 0.875rem;\n  color: $gradient2;\n}\n\n.card__link {\n  display: inline-block;\n  margin-top: 16px;\n  font-size: 0.875rem;\n  color: $super-dark-gray;\n  background-color: $primary;\n  padding: 0.2rem 0.7rem;\n  border-radius: 2rem;\n  transition: color 0.3s ease;\n\n  &:hover {\n    color: $gradient3;\n  }\n}\n\nfooter {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 24px 0;\n  font-size: 0.875rem;\n  color: $text;\n}\n\n/* Light mode styles */\nbody.light-theme {\n  background-color: $gray;\n  color: $super-dark-gray;\n\n  #logo {\n    color: $super-dark-gray;\n  }\n\n  .hero {\n    .hero__headline {\n      color: $light-gray;\n      span {\n        color: $super-dark-gray;\n      }\n    }\n\n    p {\n      color: $dim-gray;\n    }\n  }\n\n  .card {\n    background-color: $gray;\n    border-color: rgba(85, 85, 85, 0.26);\n\n    &:hover {\n      border-color: $dim-gray;\n    }\n  }\n\n  .card__header {\n    background-color: $gradient;\n  }\n\n  .card__content {\n    background-color: $gray;\n  }\n\n  .card__description {\n    color: $super-dark-gray;\n  }\n\n  .card__text {\n    color: $gradient3;\n  }\n\n  .card__link {\n    background-color: $super-dark-gray;\n    color: $accent;\n\n    &:hover {\n      color: $gradient2;\n    }\n  }\n\n  nav .btn {\n    background-color: $gradient2;\n    color: $accent;\n\n    &:hover {\n      background-color: $dim-gray;\n      color: $accent;\n    }\n  }\n\n  .theme-toggle {\n    background-color: $gradient2;\n    color: $accent;\n\n    &:hover {\n      background-color: $dim-gray;\n    }\n  }\n\n  .card__logo {\n    background-color: $gradient2;\n  }\n\n  footer {\n    color: $super-dark-gray;\n  }\n\n  hr {\n    border-top: 1px solid rgba(85, 85, 85, 0.26);\n  }\n}\n\n// media queries\n@media (min-width: 768px) {\n  nav .about {\n    display: block;\n    font-size: 1.2rem;\n  }\n\n  body {\n    padding: 24px 80px;\n  }\n\n  footer {\n    font-size: 1rem;\n    flex-direction: row;\n    gap: 16px;\n  }\n\n  .hero .hero__headline {\n    font-size: 3rem;\n    max-width: 26ch;\n  }\n}\n","@mixin flex-center {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n@mixin flex-column {\n  display: flex;\n  flex-direction: column;\n}\n\n@mixin flex-between {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n"],"sourceRoot":""}]);
